// import { render } from "@testing-library/react";
import React, { useEffect} from "react";
import * as THREE from "three/src/Three.js";
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';
import box from './box.glb';

function Scene(){
    useEffect(()=>{
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(50,1,0.1,1000);
        const renderer = new THREE.WebGLRenderer();
        document.querySelector("#box1").appendChild(renderer.domElement);
        renderer.domElement.classList.add("col-6","col-sm-6","col-md-4","col-lg-4","col-xl-3","col-xxl-3","box1Cont","rounded-circle");
        const light = new THREE.DirectionalLight("aliceblue",4);
        light.position.set(-1,0,5);
        light.castShadow = true;
        light.color.setColorName("red")
        scene.add(light);
        renderer.setClearAlpha(0);
        const loader = new GLTFLoader();
        let box1;
        let loaded = false;
        loader.load(box,(gltf)=>{
            scene.add(gltf.scene);
            gltf.scene.rotation.y = (20*Math.PI)/180;
            gltf.scene.rotation.x = (20*Math.PI)/180;
            box1 = gltf.scene;
            loaded = true;
            renderer.render(scene,camera);
        },
        undefined,(error)=>{
            console.error("an errr in loading",error)
        }
    );
        camera.position.z = 5;
        let animId;
        function face1(){
            box1.rotation.x = (200*Math.PI)/180;
            box1.rotation.y = (0*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function face2(){
            box1.rotation.x = (200*Math.PI)/180;
            box1.rotation.y = (90*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function face3(){
            box1.rotation.x = (110*Math.PI)/180;
            box1.rotation.y = (0*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function face4(){
            box1.rotation.x = (20*Math.PI)/180;
            box1.rotation.y = (0*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function face5(){
            box1.rotation.x = (290*Math.PI)/180;
            box1.rotation.y = (0*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function face6(){
            box1.rotation.x = (20*Math.PI)/180;
            box1.rotation.y = (90*Math.PI)/180;
            box1.rotation.z = (0*Math.PI)/180;
        }
        function randomResult(){
            const index = Math.floor((Math.random()*6)+1);
            return index;
        }
        const animate = ()=>{
            animId = requestAnimationFrame(animate);
            if (loaded){
                box1.rotation.y += (1*Math.PI)/180;
                box1.rotation.z += (1*Math.PI)/180;
                box1.rotation.x += (10*Math.PI)/180;
                document.querySelector("#btn").classList.add("disabled");
                setTimeout(()=>{
                    cancelAnimationFrame(animId);
                },1500);
                renderer.render(scene,camera);
        };
    }
        document.querySelector("#btn").classList.add("disabled");
        let selectedNumber;
        document.querySelector("#select").addEventListener("change",(e)=>{
            if (!isNaN(e.target.value)){
                selectedNumber = e.target.value;
                document.querySelector("#btn").classList.remove("disabled");
            }
            else{
                document.querySelector("#btn").classList.add("disabled");
            }
        })
        document.querySelector("#btn").addEventListener("click",()=>{
            animate();
            setTimeout(()=>{
                let result = randomResult();
                if (result === 1){face1()}
                else if (result === 2){face2()}
                else if (result === 3){face3()}
                else if (result === 4){face4()}
                else if (result === 5){face5()}
                else if (result === 6){face6()}
                renderer.render(scene,camera);

                if (result == selectedNumber){
                    document.querySelector(".textMessage").innerText = "You Win";
                    document.querySelector(".textMessage").classList.remove("alert-danger");
                 
                }
                else{
                    document.querySelector(".textMessage").innerText = "You Lose";
                    document.querySelector(".textMessage").classList.add("alert-danger");
                }
            },1500);
            setTimeout(()=>{
                document.querySelector("#btn").classList.remove("disabled");
            },3000);
        });
        return ()=>{
            document.querySelector("#box1").removeChild(renderer.domElement);
        }
    },[]);
    return (
        <div className="">
            <div id="box1" className="d-flex justify-content-center mt-2"></div>
            <select id="select" className="form-select w-50 m-auto my-4 bg-info">
                <option>choose number</option>
                <option value={1}>1</option>
                <option value={2}>2</option>
                <option value={3}>3</option>
                <option value={4}>4</option>
                <option value={5}>5</option>
                <option value={6}>6</option>
            </select>
            <button id="btn" className="btn btn-dark d-block col-4 m-auto">start</button>
            <h2 className="alert alert-success text-center mt-3 textMessage">?</h2>
        </div>
    );
}
export default Scene;